@import url(https://fonts.googleapis.com/css?family=Oswald:200,300,400,500,600,700&display=swap);
body{box-sizing:border-box;margin:0;font-family:"Oswald", sans-serif}*,*::before,*::after{box-sizing:inherit}::-webkit-scrollbar{width:8px}::-webkit-scrollbar-thumb{background:#7c7c7c;border-radius:1px}

.nav{width:100vw;height:80px;padding:0 16px;background:rgba(255,255,255,0.98);display:flex;justify-content:space-between;align-items:center;position:fixed;z-index:1}@media screen and (min-width: 768px){.nav{padding:0 24px}}@media screen and (min-width: 1440px){.nav{padding:0 180px}}.nav__logo{width:60px;height:60px;cursor:pointer}.nav__burger{background:none;border:none;cursor:pointer;display:none}@media screen and (min-width: 768px){.nav__burger{display:none}}.nav__list{display:flex;list-style:none}.nav__list-button{font-family:inherit;font-weight:300;font-size:16px;background:none;border:none;margin:0 0 0 35px;cursor:pointer;outline:none}.burger{width:320px;height:72px;position:fixed;top:0;right:0;background:#050505}

.hero{display:flex;flex-direction:column;justify-content:flex-end}@media screen and (min-width: 768px){.hero{flex-direction:row;justify-content:space-between}}.hero__text{margin:250px 0 80px 0}@media screen and (min-width: 768px){.hero__text{margin:250px 0 120px 0}}.hero__name{font-size:32px;font-weight:300;margin:0}.hero__title{font-size:20px;font-weight:300;margin:0}.hero__slogan{font-size:20px;font-weight:100;margin:60px 0 0 0}.hero__slogan--bolder{font-weight:400}.hero__image{height:288px;object-fit:contain;margin:72px 0;display:none}@media screen and (min-width: 768px){.hero__image{display:inline;align-self:flex-end}}

.about{margin:0 0 30px 0}.about__title{font-weight:300}.about__info{display:flex;justify-content:space-between}.about__credit{display:none}@media screen and (min-width: 768px){.about__credit{display:inline}}.about__image{display:none}@media screen and (min-width: 768px){.about__image{display:inline;width:260px;margin:0 0 20px 0}}.about__text{font-weight:200;font-size:20px;line-height:50px;margin:0;display:flex;align-items:center}

.projects{margin:0 0 20px 0;display:flex;flex-direction:column;align-items:center}.projects__title{font-weight:300;margin:0 0 50px 0;align-self:flex-start}.projects__card{width:288px;margin:0 0 30px 0;box-shadow:0 0 5px rgba(5,5,5,0.2)}@media screen and (min-width: 768px){.projects__card{width:720px;display:flex;justify-content:space-between}}@media screen and (min-width: 1440px){.projects__card{width:800px}}.projects__image{width:288px;object-fit:cover}@media screen and (min-width: 768px){.projects__image{width:450px}}.projects__info{padding:16px;display:flex;flex-direction:column;justify-content:space-between}.projects__name{font-weight:400;margin:0}.projects__description{font-weight:300;margin:0 0 10px 0}.projects__github-icon{width:36px}

.contact{margin:0 0 40px 0}.contact__title{font-weight:300}.contact__text{font-weight:300}.contact__link-img{width:50px;height:50px}

.divider{width:100%;height:1px;margin:0 auto 40px auto;background:silver}@media screen and (min-width: 768px){.divider{width:100%}}@media screen and (min-width: 1440px){.divider{width:840px}}

.main{padding:0 16px}@media screen and (min-width: 768px){.main{padding:0 24px;box-sizing:content-box;max-width:1080px;margin:0 auto}}

